import { useWatchlistName } from "@rp/react/tanstack-api/watchlist"
import { Box, Fade, Skeleton } from "@rp/react/web/mui/core"
import { ChatSuggestions } from "./ChatSuggestions"

type Props = {
  watchlistId: string
  isChatEmpty: boolean
}
const ChatWatchlistContextDisplay = ({ watchlistId, isChatEmpty }: Props) => {
  const { name, isLoading } = useWatchlistName(watchlistId)

  if (isLoading) {
    return (
      <Fade in>
        <div style={{ width: "100%" }}>
          <ChatSuggestions isChatEmpty={isChatEmpty}>
            <Skeleton width={"100%"} />
            <Skeleton width={"50%"} />
          </ChatSuggestions>
        </div>
      </Fade>
    )
  }

  return (
    <Fade in>
      <Box sx={{ "& .MuiTypography-root": { wordBreak: "break-word" } }}>
        <ChatSuggestions isChatEmpty={isChatEmpty}>
          What would you like to ask about your watchlist called "{name}"? If you want a{" "}
          <strong>summary</strong> or have <strong>specific questions</strong>, please let me know!
        </ChatSuggestions>
      </Box>
    </Fade>
  )
}

export { ChatWatchlistContextDisplay }
